.loading-wrap{
    position: fixed;
    z-index: 3;
    background: rgba(0,0,0,.5);
}
.loading-img{
    position: absolute;
    margin: auto;
    width: 1rem;
    height: 1rem;
}
.loading-img img{
    width: 100%;
    height:100%;
}


